import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import User from '@/components/User.vue'
import AboutMe from '@/components/AboutMe.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
		{
			path: '/user',
			name:'Username',
			component: User
		},
			{
			path: '/about',
			name:'AboutMe',
			component: AboutMe
		}
  ]
})



