<template>
	<el-container>

		<el-header style="width: 100%;background-color: white;">
			<el-row>
				<el-col></el-col>
				<el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect" :router="true"
				 style="width: 100%;">

					<el-menu-item style="margin-left: 100px;" class="hidden-md-and-down"></el-menu-item>
					<el-menu-item><img src="./assets/logo.png" height="40px" /></el-menu-item>
					<el-menu-item index="/" class="hidden-xs-only">首页</el-menu-item>
					<el-submenu index="2" class="hidden-xs-only">
						<template slot="title" class="hidden-xs-only">我的文章</template>
						<el-menu-item index="2-1">选项1</el-menu-item>
						<el-menu-item index="2-2">选项2</el-menu-item>
						<el-menu-item index="2-3">选项3</el-menu-item>
					</el-submenu>
					<el-menu-item index="about" class="hidden-xs-only">关于我</el-menu-item>
					<el-menu-item index="4" class="hidden-xs-only">设置</el-menu-item>
					<el-menu-item style="float: right;" class="hidden-md-and-down">
						<el-input placeholder="请输入内容" v-model="input" class="input-with-select" clearable>
							<el-button slot="append" icon="el-icon-search"></el-button>
						</el-input>
					</el-menu-item>

				</el-menu>

			</el-row>
			<router-link to="/user"></router-link>

		</el-header>

		<el-container style="height:100%;">

			<el-container>
				<el-main>
					<el-row :gutter="10">
						<el-col :span="3" class="hidden-md-and-down">&nbsp;</el-col>
						<el-col :lg="5" :sm="6" :md="7" :xs="24">
							<Aside></Aside>
							<Adver style="margin: 10px 0;"></Adver>
							<AsideList style="margin: 10px 0;"></AsideList>
						</el-col>
						<el-col :lg="13" :sm="18" :md="17" :xs="24">
							<router-view></router-view>
							</Aside>
						</el-col>
					</el-row>



				</el-main>
				<!-- 	<el-footer>

				</el-footer> -->
			</el-container>
		</el-container>
	</el-container>
	</el-container>
</template>

<script>
	import Vue from 'vue'
	import Banner from './components/Banner.vue'
	import Aside from './components/Aside.vue'
	import Adver from './components/Adver.vue'
	import AsideList from './components/AsideList.vue'
	export default {
		name: 'App',
		components: {
			Banner,
			Aside,
			AsideList,
			Adver
		},
		data: function() {
			return {
				activeIndex: '1',
				activeIndex2: '1',
				input: ''
			};
		},
		methods: {
			handleSelect(key, keyPath) {
				console.log(key, keyPath);
			}
		}

	}
</script>

<style>
	* {
		padding: 0;
		margin: 0;
	}

	.el-aside {
		display: block;
		left: 0;
		top: 60px;
		bottom: 0;
	}

	.el-header {
		line-height: 60px
	}

	.el-main {
		margin-top: -10px;
	}

	body {
		background: #F5F6F7;
	}
</style>



